import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';

export interface Dreams {
  id: string;
  position: number;
  description: string;
  private: boolean;
  status: number;
  user: string;
}

@Component({
  selector: 'app-dreams-wall',
  templateUrl: './dreams-wall.component.html',
  styleUrls: ['./dreams-wall.component.css']
})
export class DreamsWallComponent implements OnInit {

  @Output() openEvent = new EventEmitter();
  @Input() listDreams: Dreams[] = [];
  list = new Array(56).fill(1);;

  constructor() { }

  ngOnInit(): void {
  }

  haveDescription(item: any){
    let haveDesc = item.description.length > 0;
    console.log('item', item, haveDesc);
    return haveDesc;
  }

  test(){
    let l = [];

    if(this.listDreams.length > 0){
      this.list.forEach((element,i) => {
        let localized = this.listDreams.filter( x => x.position == i).length;
        if(localized > 0){
          l.push({ x: true, item: this.listDreams.find( x => x.position == i)})
        }
        else{
          l.push({ x: false, item: ""})
        }
      });
    }
    else{
      l.push({ x: false, item: ""});
    }

    return l;
  }

  EmitterAdd(id){
    // console.log('DreamsWallComponent -> ', id);
    this.openEvent.emit(id);
  }
}
