import { Component, EventEmitter, Inject, OnInit, Output } from '@angular/core';
import { FormControl, FormGroup } from '@angular/forms';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material/dialog';
import { AppComponent } from 'src/app/app.component';

export interface DialogData {
  animal: string;
  name: string;
}

@Component({
  selector: 'app-register-dream',
  templateUrl: './register-dream.component.html',
  styleUrls: ['./register-dream.component.css']
})
export class RegisterDreamComponent implements OnInit {

  Registrar = false;
  isChecked = true;
  state = 'Public';
  formUser: FormGroup;

    constructor(
    public dialogRef: MatDialogRef<AppComponent>,
    @Inject(MAT_DIALOG_DATA) public data: DialogData) {}

  ngOnInit(): void {
    this.formUser = new FormGroup({
      userId: new FormControl(),
      myDream: new FormControl(),
      isPrivate: new FormControl()
    });

    this.ViewChangeForm();
  }

  ViewChangeForm(){
    this.formUser.valueChanges.subscribe(() => {
      let isPrivate = this.formUser.get('isPrivate').value;
      if(isPrivate)
        this.state = 'Private';
      else
        this.state = 'Public';
    });
  }

  SignIn(){
    console.log(this.formUser.value);
    const user = this.formUser.get('email').value;
    console.log('userr ',user);
    this.dialogRef.close(user);
  }

  onNoClick(): void {
    this.Registrar = !this.Registrar;
    // console.log(this.formUser.value);
    // this.dialogRef.close();
  }

  Close(){
    this.dialogRef.close();
  }

}
