import { UserService } from './../services/user.service';
import { Component, EventEmitter, Inject, OnInit, Output } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material/dialog';
import { AppComponent } from 'src/app/app.component';
import { User } from 'src/models/user';

export interface DialogData {
  animal: string;
  name: string;
}

@Component({
  selector: 'app-sign-in',
  templateUrl: './sign-in.component.html',
  styleUrls: ['./sign-in.component.css']
})
export class SignInComponent implements OnInit {

  Registrar = false;
  isChecked = true;
  submitted = false;
  formUser: FormGroup;
  users: User[];

    constructor(
    public dialogRef: MatDialogRef<AppComponent>,
    @Inject(MAT_DIALOG_DATA) public data: DialogData,
    private userServ: UserService) {}

  ngOnInit(): void {
    this.formUser = new FormGroup({
      email: new FormControl('',[Validators.required, Validators.email]),
      senha: new FormControl('',[Validators.required, Validators.minLength(6)]),
      apelido: new FormControl(),
      passwordConfirm: new FormControl(),
      pais: new FormControl(),
      idade: new FormControl(),
      ehCadastro: new FormControl()
    });
    // this.ViewChangeForm();
  }

  get f() { return this.formUser.controls; }

  onSubmit() {
    console.log('submited');
    this.submitted = true;

    if (this.formUser.invalid) {
        return;
    }
    this.SignIn();

    this.GetUserById(2);

    this.GetUsers();
  }

  GetUsers(){
    this.userServ.getUsers().subscribe( ret => {
      console.log('Retorno todos usuarios', ret);
    })
  }

  GetUserById(id: any){
    this.userServ.getUserById(id).subscribe( ret => {
      console.log('Retorno por id', ret);
    })
  }


  ViewChangeForm(){
    this.formUser.valueChanges.subscribe(() => {
      console.log(this.formUser.value);
    });
  }

  SignIn(){
    console.log('SignIn Success!! :-\n\n' + JSON.stringify(this.formUser.value));
    let user = this.formUser.get('email').value;
    user = user.substring(0,user.indexOf('@'));
    console.log('userr ',user);
    this.dialogRef.close(user);
  }

  onNoClick(): void {
    this.Registrar = !this.Registrar;
    // console.log(this.formUser.value);
    // this.dialogRef.close();
  }

}
